import { Component, OnInit } from '@angular/core';
import {InventoryServiceService} from 'src/services/applicationServices/inventory-service.service'
import { Router } from '@angular/router';

@Component({
  selector: 'app-add-purchase',
  templateUrl: './add-purchase.component.html',
  styleUrls: ['./add-purchase.component.css']
})
export class AddPurchaseComponent implements OnInit {

  purchaseData :any = {
    items:[]
  }

  constructor(private _invService: InventoryServiceService,
               private _router: Router) { }

  ngOnInit(): void {
  }

  savePurchase(){
    console.log(this.purchaseData);

    this._invService.addPurchase(this.purchaseData).subscribe(
      res=>{
        console.log(res);
        console.log("Purchase Saved");
        this._router.navigate(['/inventory-purchase']);
      },
      err=>{
        console.log(err);
        this._router.navigate(['/inventory-purchase']);
      }
    )
  }

  addItem(){
    this.purchaseData.items.push({itemName:'',details:'',count:''});
  }
  removeItem(i:any){
    this.purchaseData.items.splice(i,1)
  }
}
