import { Component, OnInit } from '@angular/core';
import {InventoryServiceService} from 'src/services/applicationServices/inventory-service.service'
import {ZonesService} from 'src/services/applicationServices/zones.service'
import { Router } from '@angular/router';
@Component({
  selector: 'app-add-transaction',
  templateUrl: './add-transaction.component.html',
  styleUrls: ['./add-transaction.component.css']
})
export class AddTransactionComponent implements OnInit {

  transData :any = { items:[]  }

  zones : any[]


  constructor(private _invService: InventoryServiceService,
               private _router: Router,
               private _zonesService: ZonesService) { }

  ngOnInit(): void {
    let result: any[];
    this._zonesService.getZones()
      .subscribe(
        res=>{
          result = JSON.parse(res);
          this.zones=result;
        },
        err=>console.log(err)
      )

  }

  saveTransaction(){
    console.log(this.transData);

    this._invService.addTransaction(this.transData).subscribe(
      res=>{
        console.log(res);
        console.log("Transaction Saved");
        this._router.navigate(['/inventory-transactions']);
      },
      err=>{
        console.log(err);
        this._router.navigate(['/inventory-transactions']);
      }
    )
  }

  addItem(){
    this.transData.items.push({itemName:'',details:'',count:''});
  }
  removeItem(i:any){
    this.transData.items.splice(i,1)
  }
}
