import { Component, OnInit } from '@angular/core';
import {InventoryServiceService} from 'src/services/applicationServices/inventory-service.service'
@Component({
  selector: 'app-inventory-transactions',
  templateUrl: './inventory-transactions.component.html',
  styleUrls: ['./inventory-transactions.component.css']
})
export class InventoryTransactionsComponent implements OnInit {

  transactions = []
  constructor(private _inventoryService: InventoryServiceService) { }

  ngOnInit(): void {
    let result: any[];
    this._inventoryService.getTransactions()
      .subscribe(
        res=>{
          result = JSON.parse(res);
          this.transactions=result;
        },
        err=>console.log(err)
      )
  }

}
