import { Component, OnInit } from '@angular/core';
import {InventoryServiceService} from 'src/services/applicationServices/inventory-service.service'

@Component({
  selector: 'app-inventory-master',
  templateUrl: './inventory-master.component.html',
  styleUrls: ['./inventory-master.component.css']
})
export class InventoryMasterComponent implements OnInit {

  inventoryList = []
  constructor(private _inventoryService: InventoryServiceService) { }

  ngOnInit(): void {
    let result: any[];
    this._inventoryService.getInventoryList()
      .subscribe(
        res=>{
          result = JSON.parse(res);
          this.inventoryList=result;
        },
        err=>console.log(err)
      )
  }

}
