import { Injectable } from '@angular/core';
import { HttpService } from 'src/services/applicationServices/httpService/http.service';

@Injectable({
  providedIn: 'root'
})
export class InventoryServiceService {

  constructor(private _httpService: HttpService) { }

  getInventoryList() {
    return this._httpService.get("/api/v1/inventory");
  }

  getPurchases() {
    return this._httpService.get("/api/v1/purchases");
  }

  getTransactions() {
    return this._httpService.get("/api/v1/transactions");
  }

  getDisposals() {
    return this._httpService.get("/api/v1/disposals");
  }

  ////////////////////////

  addPurchase(purchase) {
    return this._httpService.post("/api/v1/purchases",purchase);
  }

  addTransaction(trans) {
    return this._httpService.post("/api/v1/transactions",trans);
  }

  addDisposal(disposal) {
    return this._httpService.post("/api/v1/disposals",disposal);
  }

}
