import { Component, OnInit } from '@angular/core';
import {ZonesService} from 'src/services/applicationServices/zones.service'
import { Router } from '@angular/router';

@Component({
  selector: 'app-add-zone',
  templateUrl: './add-zone.component.html',
  styleUrls: ['./add-zone.component.css']
})
export class AddZoneComponent implements OnInit {

  zone :any = {}
  constructor(private _zoneService: ZonesService,
               private _router: Router) { }

  ngOnInit(): void {
  }

  saveZone(){
    this._zoneService.addZone(this.zone).subscribe(
      res=>{
        console.log(res);
        console.log("Zone Saved");
        this._router.navigate(['/zones-master']);
      },
      err=>{
        console.log(err);
        this._router.navigate(['/zones-master']);
      }
    )
  }
}
