import { Component, OnInit } from '@angular/core';
import {InventoryServiceService} from 'src/services/applicationServices/inventory-service.service'
@Component({
  selector: 'app-inventory-disposal',
  templateUrl: './inventory-disposal.component.html',
  styleUrls: ['./inventory-disposal.component.css']
})
export class InventoryDisposalComponent implements OnInit {

  disposals = []
  constructor(private _inventoryService: InventoryServiceService) { }

  ngOnInit(): void {
    let result: any[];
    this._inventoryService.getDisposals()
      .subscribe(
        res=>{
          result = JSON.parse(res);
          this.disposals=result;
        },
        err=>console.log(err)
      )
  }

}
