import { Component, OnInit } from '@angular/core';
import {InventoryServiceService} from 'src/services/applicationServices/inventory-service.service'

@Component({
  selector: 'app-inventory-purchase',
  templateUrl: './inventory-purchase.component.html',
  styleUrls: ['./inventory-purchase.component.css']
})
export class InventoryPurchaseComponent implements OnInit {

  purchases = []
  constructor(private _inventoryService: InventoryServiceService) { }

  ngOnInit(): void {
    let result: any[];
    this._inventoryService.getPurchases()
      .subscribe(
        res=>{
          result = JSON.parse(res);
          this.purchases=result;
        },
        err=>console.log(err)
      )
  }

}
