import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './Component/account/login/login.component';
import { MyAccountComponent } from './Component/account/my-account/my-account.component';
import { SignupComponent } from './Component/account/signup/signup.component';
import { LayoutComponent } from './Component/layout/layout/layout.component';
import { DashboardComponent } from './Component/dashboard/dashboard.component';
import { ForgotPasswordComponent } from './Component/account/forgot-password/forgot-password.component';
import { ResetPasswordComponent } from './Component/account/reset-password/reset-password.component';
import { InventoryMasterComponent } from './Component/inventory-master/inventory-master.component';
import { ReportsComponent } from './Component/reports/reports.component';


import { InventoryPurchaseComponent } from './Component/purchases/inventory-purchase/inventory-purchase.component';
import { InventoryTransactionsComponent } from './Component/transactions/inventory-transactions/inventory-transactions.component';
import { InventoryDisposalComponent } from './Component/disposals/inventory-disposal/inventory-disposal.component';
import { ZonesInventoryComponent } from './Component/zones-inventory/zones-inventory.component';
import { ZonesMasterComponent } from './Component/zones/zones-master/zones-master.component';
import { AddZoneComponent } from './Component/zones/add-zone/add-zone.component';
import { AddDisposalComponent } from './Component/disposals/add-disposal/add-disposal.component';
import { AddPurchaseComponent } from './Component/purchases/add-purchase/add-purchase.component';
import { AddTransactionComponent } from './Component/transactions/add-transaction/add-transaction.component';

const routes: Routes = [
  { path: '', pathMatch: 'full', redirectTo: 'login' },
  { path: 'login', component: LoginComponent },
  { path: 'forgot-password', component: ForgotPasswordComponent },
  { path: 'reset-password', component: ResetPasswordComponent },
  { path: 'signup', component: SignupComponent },
  {
    path: '', component: LayoutComponent,
    children: [
      { path: 'dashboard', component: DashboardComponent },
      { path: 'inventory-master', component: InventoryMasterComponent },
      { path: 'my-account', component: MyAccountComponent },
      { path: 'reports', component: ReportsComponent },
      { path: 'inventory-transactions', component: InventoryTransactionsComponent },
      { path: 'inventory-disposal', component: InventoryDisposalComponent },
      { path: 'inventory-purchase', component: InventoryPurchaseComponent },
      { path: 'zones-inventory', component: ZonesInventoryComponent },
      { path: 'zones-master', component: ZonesMasterComponent },
      { path: 'add-zone', component: AddZoneComponent },
      { path: 'add-purchase', component: AddPurchaseComponent },
      { path: 'add-disposal', component: AddDisposalComponent },
      { path: 'add-transaction', component: AddTransactionComponent },
      ]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true, onSameUrlNavigation: 'reload' })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
