import { Injectable } from '@angular/core';
import { HttpService } from 'src/services/applicationServices/httpService/http.service';

@Injectable({
  providedIn: 'root'
})
export class AccountService {

  constructor(private _httpService: HttpService) { }
 
  Login(LoginModel) {
    return this._httpService.post("/authenticate", LoginModel);
  }

  Register(RegisterUserModel) {
    return this._httpService.post("/register", RegisterUserModel);
  }

  loggedIn(){
    return !!localStorage.getItem('token');
  }

  getToken(){
    return localStorage.getItem('token');
  }
}
