import { Injectable } from '@angular/core';
import { HttpService } from 'src/services/applicationServices/httpService/http.service';

@Injectable({
  providedIn: 'root'
})
export class ZonesService {

  constructor(private _httpService: HttpService) { }

  getZones(){
    return this._httpService.get("/api/v1/zones");
  }

  getZonesInventory(){
    return this._httpService.get("/api/v1/zones/inventory");
  }

  addZone(zone){
    return this._httpService.post("/api/v1/zones",zone);
  }
}
