import { Component, OnInit } from '@angular/core';
import {ZonesService} from 'src/services/applicationServices/zones.service'

@Component({
  selector: 'app-zones-inventory',
  templateUrl: './zones-inventory.component.html',
  styleUrls: ['./zones-inventory.component.css']
})
export class ZonesInventoryComponent implements OnInit {

  zonalInventory = []
  constructor(private _zonesService: ZonesService) { }

  ngOnInit(): void {
    let result: any[];
    this._zonesService.getZonesInventory()
      .subscribe(
        res=>{
          result = JSON.parse(res);
          this.zonalInventory=result;
        },
        err=>console.log(err)
      )
  }

}
