import { Component, OnInit } from '@angular/core';
import {ZonesService} from 'src/services/applicationServices/zones.service'

@Component({
  selector: 'app-zones-master',
  templateUrl: './zones-master.component.html',
  styleUrls: ['./zones-master.component.css']
})
export class ZonesMasterComponent implements OnInit {

  zones = []
  constructor(private _zonesService: ZonesService) { }

  ngOnInit(): void {
    let result: any[];
    this._zonesService.getZones()
      .subscribe(
        res=>{
          result = JSON.parse(res);
          this.zones=result;
        },
        err=>console.log(err)
      )
  }

}
