import { Component, OnInit } from '@angular/core';
import {InventoryServiceService} from 'src/services/applicationServices/inventory-service.service'
import { Router } from '@angular/router';

@Component({
  selector: 'app-add-disposal',
  templateUrl: './add-disposal.component.html',
  styleUrls: ['./add-disposal.component.css']
})
export class AddDisposalComponent implements OnInit {

  disposalData :any = {  items:[] }
  constructor(private _invService: InventoryServiceService,
               private _router: Router) { }

  ngOnInit(): void {
  }

  saveDisposal(){
    
    console.log(this.disposalData);

    this._invService.addDisposal(this.disposalData).subscribe(
      res=>{
        console.log(res);
        console.log("Disposal Saved");
        this._router.navigate(['/inventory-disposal']);
      },
      err=>{
        console.log(err);
        this._router.navigate(['/inventory-disposal']);
      }
    )
  }

  addItem(){
    this.disposalData.items.push({itemName:'',details:'',count:''});
  }
  removeItem(i:any){
    this.disposalData.items.splice(i,1)
  }

}
