import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from "src/environments/environment";
import { map } from "rxjs/operators";

@Injectable({
  providedIn: 'root'
})
export class HttpService {
  constructor(private _http: HttpClient) {
    
  }

  get(url: string, IsShowPageLoader: boolean = false) {
    let myheader = new HttpHeaders().set('IsShowPageLoader', IsShowPageLoader ? "true" : "false")
  myheader.set('Content-Type', 'application/json');
     return this._http.get(environment.baseUrl + url, { headers: myheader , responseType: 'text'}).pipe(map(res => {
      return res;
    }));
  }
  post(url: string, data: any, IsShowPageLoader: boolean = false) {
    let myheader = new HttpHeaders().set('IsShowPageLoader', IsShowPageLoader ? "true" : "false")
    myheader.set('Content-Type', 'application/json');
    return this._http.post(environment.baseUrl + url, data, { headers: myheader }).pipe(map(res => {
      return res;
    }));
  }
}
